.paper {
    background-color: var(--bgBody) !important;
    border-radius: 10px;
}

.divider {
    border-color: var(--colorSecond) !important;
}

.eat-card {
    cursor: pointer;
    border-bottom: 1px solid var(--colorSecond) !important;
    padding-bottom: 1.75rem !important;
    padding: 2rem 1rem;
}
.eat-card * {
    cursor: pointer;
}
.eat-card:hover {
    background-color: var(--bgSecond) !important;
}

.chip-rate span {
    padding-left: 8px !important;
}

.MuiChip-filled span {
    color: var(--colorWhite) !important;
}