@media (max-width: 550px) {
    .divider {
        visibility: hidden;
    }

    .eat-card {
        gap: 0.25rem !important;
    }
    .eat-card > * {
        width: 100% !important;
    }

    .eat-info {
        gap: 1rem !important;
    }
    .eat-info h4 {
        width: 100% !important;
    }

    * {
        font-size: small;
    }
}